import { CACHE_FOOT_HEIGHT } from '../../actions/constant';

function footer( state = { footHeight: 0}, action){
//  action
  switch (action.type){
    case CACHE_FOOT_HEIGHT:
      //    head
      return Object.assign({}, state, {footHeight: action.payload});
    default:
      return state;
  }
}

export default footer;


