import { CACHE_HEAD_HEIGHT } from '../../actions/constant';

function header( state = { headHeight: 0}, action){
//  action
  switch (action.type){
    case CACHE_HEAD_HEIGHT:
  //    head
      return Object.assign({}, state, {headHeight: action.payload});
    default:
      return state;
  }
}

export default header;


